	module.exports = {"data":{"challengeNode":{"title":"Change Animation Timing with Keywords","description":["在 CSS 动画里，<code>animation-timing-function</code>规定动画的速度曲线。速度曲线定义动画从一套 CSS 样式变为另一套所用的时间。如果要描述的动画是一辆车在指定时间内（<code>animation-duration</code>）从 A 运动到 B，那么<code>animation-timing-function</code>表述的就是车在运动中的加速和减速等过程。","已经有了很多预定义的值可以直接使用于大部分场景。比如，默认的值是<code>ease</code>，动画以低速开始，然后加快，在结束前变慢。其它常用的值包括<code>ease-out</code>，动画以高速开始，以低速结束;<code>ease-in</code>，动画以低速开始，以高速结束；<code>linear</code>，动画从头到尾的速度是相同的。","<hr>","给 id 为<code>ball1</code>和<code>ball2</code>的元素添加<code>animation-timing-function</code>，<code>ball1</code>赋值为<code>linear</code>，<code>ball2</code>赋值为<code>ease-out</code>。它们的<code>animation-duration</code>都为 2 秒，注意观察它们在开始和结束时的不同。"],"challengeType":0,"fields":{"slug":"/responsive-web-design/applied-visual-design/change-animation-timing-with-keywords","blockName":"Applied Visual Design","tests":[{"text":"id 为<code>ball1</code>的元素的<code>animation-timing-function</code>属性值应该为 linear。","testString":"assert($('#ball1').css('animation-timing-function') == 'linear', 'id 为<code>ball1</code>的元素的<code>animation-timing-function</code>属性值应该为 linear。');"},{"text":"id 为<code>ball2</code>的元素的<code>animation-timing-function</code>属性值应该为 ease-out。","testString":"assert($('#ball2').css('animation-timing-function') == 'ease-out', 'id 为<code>ball2</code>的元素的<code>animation-timing-function</code>属性值应该为 ease-out。');"}]},"required":[],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<style>\n\n  .balls {\n    border-radius: 50%;\n    background: linear-gradient(\n      35deg,\n      #ccffff,\n      #ffcccc\n    );\n    position: fixed;  \n    width: 50px;\n    height: 50px;\n    margin-top: 50px;\n    animation-name: bounce;\n    animation-duration: 2s;\n    animation-iteration-count: infinite;\n  }\n  #ball1 { \n    left:27%;\n    \n  }\n  #ball2 { \n    left:56%;\n    \n  }\n\n@keyframes bounce {\n  0% {\n    top: 0px;\n  } \n  100% {\n    top: 249px;\n  }\n} \n\n</style>\n\n<div class=\"balls\" id=\"ball1\"></div>\n<div class=\"balls\" id=\"ball2\"></div>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/responsive-web-design/applied-visual-design/learn-how-bezier-curves-work","id":"587d78a8367417b2b2512ae7"},"slug":"/responsive-web-design/applied-visual-design/change-animation-timing-with-keywords"}}

